import type { Metadata } from "next";
import { Geist, Geist_Mono } from "next/font/google";
import "./globals.css";
import Header from "@/components/Header";

const geistSans = Geist({
  variable: "--font-geist-sans",
  subsets: ["latin"],
});

const geistMono = Geist_Mono({
  variable: "--font-geist-mono",
  subsets: ["latin"],
});

export const metadata: Metadata = {
  title: "Mi Sitio con Strapi",
  description: "Generado con Next.js y Strapi",
};

// 1. Función para obtener los datos del Header
async function getHeaderData() {
  const url = `${process.env.NEXT_PUBLIC_STRAPI_URL}/api/header?populate=*`;
  
  try {
    const res = await fetch(url, { cache: 'no-store' });
    const json = await res.json();
    return json.data;
  } catch (error) {
    console.error("Error cargando el Header:", error);
    return null;
  }
}

export default async function RootLayout({
  children,
}: Readonly<{
  children: React.ReactNode;
}>) {
  // 2. Ejecutamos la carga de datos
  const headerData = await getHeaderData();

  return (
    <html lang="es">
      <body className={`${geistSans.variable} ${geistMono.variable} antialiased`}>
        {/* 3. Insertamos el Header arriba de todo */}
        {headerData && (
          <Header 
            logo={headerData.logo} 
            menu_links={headerData.menu_links} 
            boton_accion={headerData.boton_accion} 
          />
        )}
        
        {/* El contenido de nuestras páginas va aquí */}
        <div className="pt-20"> {/* Padding superior para que el Header fix no tape el contenido */}
          {children}
        </div>
      </body>
    </html>
  );
}